
.loadingDotsBlack:after {
    content: ' .';
    animation: dotsBlack 1s steps(5, end) infinite;}
  
  @keyframes dotsBlack {
    0%, 20% {
      color: #333333;
      text-shadow:
        .25em 0 0 rgba(0,0,0,0),
        .5em 0 0 rgba(0,0,0,0);}
    40% {
      color: white;
      text-shadow:
        .25em 0 0 rgba(0,0,0,0),
        .5em 0 0 rgba(0,0,0,0);}
    60% {
      text-shadow:
        .25em 0 0 #333333,
        .5em 0 0 rgba(0,0,0,0);}
    80%, 100% {
      text-shadow:
        .25em 0 0 #333333,
        .5em 0 0 #333333;}}